___CSS_LOADER_EXPORT___.push([module.id, `#header-logo {
  width: clamp(25px, 10vw, 75px);
  filter: drop-shadow(0px 0px 2px rgba(0, 0, 0, 0.5));
}

#header-logo:hover {
  cursor: pointer;
}

#header-search-bar {
  border: none;
}

#display-page {
  width: clamp(100px, 100%, 700px);
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 0px 20px;
}

#snapshot {
  background-color: rgba(0, 0, 0, 0.1);
  padding-bottom: 30px;
  display: grid;
  justify-items: center;
  grid-template-rows: 4rem 1fr;
  grid-template-columns: 1fr 1fr;
  border-radius: 5px;
}

#cityName {
  width: 100%;
  height: 3rem;
  text-align: center;
  background-color: rgba(var(--main-color), 0.8);
  margin: 0;
  grid-column: 1 / 4;
}

#snapshot-left, #snapshot-right {
  padding: 10px 0px;
}

#snapshot-left {
  display: flex;
  align-items: center;
}

#snapshot-icon {
  height: clamp(30px, 100%, 75px);
}

#left-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px 10px;
}

#currTemp {
  font-size: 2.5rem;
  font-weight: 600;
}

#tempRange {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5px;
}

#snapshot-right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

#weekly-report {
  height: clamp(50px, 10vh, 100px);
  display: grid;
  grid-template-columns: repeat(6, minmax(80px,1fr));
  grid-template-rows: 1fr;
  gap: 3px;
  overflow: auto;
}


.daily-report-date {
  font-weight: 600;
}

.daily-report {
  background-color: rgba(var(--main-color), 0.5);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

#advisory-btn {
  width: 100%;
  color: white;
  font-weight: 900;
  background-color: rgba(255, 0, 0, 0.8);
  border: 2px solid red;
  border-radius: 5px;
}

#advisory-btn:hover {
  background-color: rgba(255, 0, 0);
}

.alert-event,
.alert-description {
  padding-left: 1rem;
  padding-right: 1rem;
}

.alert-event {
  color: red;
  font-size: 1.25rem;
  border: 2px solid red;
}

.alert-description {
  background-color: rgba(255, 0, 0, 0.05);
  border-left: 7px solid red;
  margin: 5px 0px 15px 5px;
}
