___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --main-color: 70, 130, 180;
}

* {
  font-family: "lexend", system-ui, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
}

body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
  overflow: auto;
}

#header {
  background-color: rgba(var(--main-color));
  box-shadow: -5px 3px 10px rgba(0, 0, 0, 0.4);
  position: sticky;
  top: 0;
  display: inline-flex;
  justify-content: center;
  padding: 10px 0px;
  margin-bottom: 15px;
}

#header,
#content {
  width: 100vw;
  margin-right: auto;
  margin-left: auto;
}

#content {
  display: inline-flex;
  justify-content: center;
}

.background {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  opacity: 0.85;
}

.search-container {
  display: flex;
  align-items: center;
  position: relative;
}

.search-bar {
  font-size: 1.5rem;
  height: 2.5rem;
  width: clamp(200px, 80vw, 600px);
  padding: 0.25rem 2.5rem 0.25rem 0.75rem;
  border-radius: 10px;
  border: none;
  margin-left: 15px;
}

.search-btn {
  width: 2rem;
  background: transparent;
  border: none;
  position: absolute;
  transform: scale(1.25);
  right: 0.25rem;
}

button {
  cursor: pointer;
}
